.header {
    display: flex;
    justify-content: space-between;
    position: sticky;
    top: 0;
    min-height: 4.563rem;
    background: #fff;
    box-shadow: 0 0 1.25rem 0 rgb(0 0 0 / 10%);
    z-index: 100;
}


.header img {
    padding: 15px 10px 0 10px;
    height: 2.813rem;
}

.logo {
    display: flex;
    vertical-align: center;
}

a {
    text-decoration: none;
}

.logo span {
    padding-top: 15px;
    font-family: 'Poppins', sans-serif;
    margin-left: 0.625rem;
    font-size: 1.75rem;
    color: #3f414d;
    cursor: pointer;
}

.login {
    padding: 15px;
    font-family: 'Poppins', sans-serif;
    font-size: 20px;

}

.login button {
min-width: 100px;
min-height: 30px;
    font-family: 'Poppins', sans-serif;
font-weight: 600;
    color: #50b5ff;
    background-color: rgba(80,181,255,.1);
    border-radius: 5px;

}